<template>
    <div>
        <div slot="title" style="padding-left: 10px">
            <el-select v-model="selectedTaskIndex" placeholder="任务" @change="displayTask">
                <el-option
                    v-for="(item,index) in tasks"
                    :key="item.namespace"
                    :label="item.name"
                    :value="index">
                </el-option>
            </el-select>
            <i class="ml-5 c-blue cursor-pointer" @click="load" :class="{'el-icon-refresh':!isLoading,'el-icon-loading':isLoading}"></i>
        </div>

        <div class="block pd-10">
            <el-descriptions class="margin-top" direction="vertical" title="" :column="15" border>
                <el-descriptions-item :labelStyle="{'text-align': 'center'}" :contentStyle="{'text-align': 'center'}">
                    <template slot="label">
                        <i class="el-icon-c-scale-to-original"></i>
                        脚本
                    </template>
                    <span>{{ selectedTask.namespace }}</span>
                </el-descriptions-item>
                <el-descriptions-item :labelStyle="{'text-align': 'center'}" :contentStyle="{'text-align': 'center'}">
                    <template slot="label">
                        <i class="el-icon-time"></i>
                        创建时间
                    </template>
                    {{ selectedTask.created |HumanizedTime }}
                </el-descriptions-item>
                <el-descriptions-item :labelStyle="{'text-align': 'center'}" :contentStyle="{'text-align': 'center'}">
                    <template slot="label">
                        <i class="el-icon-attract"></i>
                        运行模式
                    </template>
                    {{ selectedTask.mode|statusTransfer('task_run_mode') }}
                </el-descriptions-item>
                <el-descriptions-item :labelStyle="{'text-align': 'center'}" :contentStyle="{'text-align': 'center'}">
                    <template slot="label">
                        <i class="el-icon-time"></i>
                        运行时间
                    </template>
                    {{ selectedTask.mode === 1 || selectedTask.mode === 3 ? `每${selectedTask.interval_humanize}` : selectedTask.mode === 2 ? (selectedTask.override ? selectedTask.override.times.join(',') : selectedTask.times.join(',')) : '单次执行' }}
                </el-descriptions-item>
                <el-descriptions-item :labelStyle="{'text-align': 'center'}" :contentStyle="{'text-align': 'center'}">
                    <template slot="label">
                        <i class="el-icon-check"></i>
                        运行次数
                    </template>
                    {{ selectedTask.run_count }}
                </el-descriptions-item>
                <el-descriptions-item :labelStyle="{'text-align': 'center'}" :contentStyle="{'text-align': 'center'}">
                    <template slot="label">
                        <i class="el-icon-time"></i>
                        最近运行
                    </template>
                    {{ selectedTask.last_run || '--' }}
                </el-descriptions-item>
                <el-descriptions-item :contentStyle="{'text-align': 'center'}">
                    <template slot="label">
                        <i class="el-icon-time"></i>
                        下次运行
                    </template>
                    {{ selectedTask.next_run | HumanizedTime }}
                </el-descriptions-item>
                <el-descriptions-item :labelStyle="{'text-align': 'center'}" :contentStyle="{'text-align': 'center'}">
                    <template slot="label">
                        <i class="el-icon-alarm-clock"></i>
                        计时器
                    </template>
                    {{ selectedTask.ticker || '--' }}
                </el-descriptions-item>
                <el-descriptions-item :labelStyle="{'text-align': 'center'}" :contentStyle="{'text-align': 'center'}">
                    <template slot="label">
                        <i class="el-icon-magic-stick"></i>
                        任务
                    </template>
                    <el-tag :type="selectedTask.real_status === 1?'success':'info'">{{ selectedTask.real_status === 1 ? '启用' : '停用' }}</el-tag>
                </el-descriptions-item>
                <el-descriptions-item :labelStyle="{'text-align': 'center'}" :contentStyle="{'text-align': 'center'}" v-if="selectedTask.real_status===1">
                    <template slot="label">
                        <i class="el-icon-s-unfold"></i>
                        状态
                    </template>
                    <el-tag :type="selectedTask.is_busy === 1?'warning':'success'">{{ selectedTask.is_busy === 1 ? '正在执行' : '准备就绪' }}</el-tag>
                </el-descriptions-item>
                <el-descriptions-item :labelStyle="{'text-align': 'center'}" :contentStyle="{'text-align': 'center'}">
                    <template slot="label">
                        <i class="el-icon-menu"></i>
                        操作
                    </template>
                    <el-tooltip class="item" effect="dark" content="立即执行此任务一次,此操作不会重置下一次自动运行的时间" placement="top">
                        <el-button icon="el-icon-video-play" @click="run" size="mini" :disabled="selectedTask.is_busy !== 0"></el-button>
                    </el-tooltip>
                    <el-button icon="el-icon-setting" @click="editTask" size="mini"></el-button>
                </el-descriptions-item>
            </el-descriptions>
            <div class="pt-20 pb-20">
                <el-date-picker
                    v-model="searchParams.day"
                    type="date"
                    format="yyyy-MM-dd"
                    value-format="yyyy-MM-dd"
                    @change="changeDay()"
                    class="fr"
                    :editable="false"
                    :clearable="false"
                    :picker-options="pickerOptions"
                    placeholder="选择日期">
                </el-date-picker>
            </div>
            <el-divider></el-divider>
            <el-empty v-if="!logs.length" :image-size="150" description="暂无运行日志"></el-empty>
            <el-timeline v-if="logs.length">
                <el-timeline-item placement="top" v-for="(item,index) in logs" :timestamp="item.date" :key="index">
                    <el-card>
                        <p style="margin-top: 5px;word-break: break-all;">{{ item.message }}</p>
                    </el-card>
                </el-timeline-item>
            </el-timeline>
        </div>
        <!--翻页-->
        <el-pagination
            v-if="logs.length"
            background
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page.sync="searchParams.page"
            :page-sizes="[6, 20, 50,100]"
            :page-size="searchParams.size"
            layout="total,sizes, prev, pager, next"
            :total="searchParams.count">
        </el-pagination>
        <!--表单弹框-->
        <el-dialog title="任务设置" :visible.sync="dialogVisible" :close-on-click-modal="false" width="50%">
            <el-form :model="crontabConfig" status-icon ref="form" label-width="120px" class="">
                <el-form-item label="任务脚本：" prop="namespace">
                    {{ crontabConfig.namespace }}
                </el-form-item>
                <el-form-item label="任务名称：" prop="name">
                    <el-col :span="10">
                        <el-input type="text" placeholder="任务名称" v-model="crontabConfig.name"></el-input>
                    </el-col>
                </el-form-item>

                <el-form-item label="执行方式：" prop="mode">
                    <el-col :span="24">
                        <el-radio-group v-model="crontabConfig.mode">
                            <el-radio :label="1">循环执行{{ selectedTask.mode === 1 ? '(默认)' : '' }}</el-radio>
                            <el-radio :label="3">定时循环{{ selectedTask.mode === 3 ? '(默认)' : '' }}</el-radio>
                            <el-radio :label="2">定时执行{{ selectedTask.mode === 2 ? '(默认)' : '' }}</el-radio>
                        </el-radio-group>
                    </el-col>
                </el-form-item>

                <el-form-item label="执行时间：" prop="times" v-if="crontabConfig.mode===2">
                    <el-col :span="24">
                        <el-tag v-for="(time,index) in selectedTask.times" v-bind:key="index" :class="{'ml-5':index!==0}">{{ time }}</el-tag>
                    </el-col>
                    <el-col :span="24">
                        <el-select
                            style="width: 400px"
                            v-model="crontabConfig.times"
                            multiple
                            filterable
                            allow-create
                            default-first-option
                            size="medium"
                            placeholder="执行时间">
                            <el-option
                                v-for="item in timesOptions"
                                :key="item.value"
                                :label="item.label"
                                :value="item.value">
                            </el-option>
                        </el-select>

                        <!--                        <el-input type="textarea" :rows="3" placeholder="10:00,12:30"-->
                        <!--                                  v-model="crontabConfig.times"></el-input>-->
                    </el-col>
                </el-form-item>
                <el-form-item label="间隔时间：" prop="interval" v-else>
                    <el-col :span="15">
                        <el-input type="number" placeholder="单位:秒" v-model="crontabConfig.interval">
                            <template slot="append">秒</template>
                        </el-input>
                        默认:<b>{{ selectedTask.interval }}</b>秒
                    </el-col>
                </el-form-item>

                <el-form-item label="任务状态：" prop="status">
                    <el-radio-group v-model="crontabConfig.status">
                        <el-radio :label="1">开启</el-radio>
                        <el-radio :label="0">关闭</el-radio>
                    </el-radio-group>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible = false">取 消</el-button>
                <el-button type="primary" @click="saveTask()" :loading="isLoading">保 存</el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
import moment from "moment";

export default {
    name: "Crontab",
    data() {
        return {
            isPageActivation: false,
            refreshTimer: null,
            dialogVisible: false,
            pickerOptions: {
                disabledDate(time) {
                    return time.getTime() > Date.now();
                },
                shortcuts: [{
                    text: '今天',
                    onClick(picker) {
                        picker.$emit('pick', new Date());
                    }
                }, {
                    text: '昨天',
                    onClick(picker) {
                        const date = new Date();
                        date.setTime(date.getTime() - 3600 * 1000 * 24);
                        picker.$emit('pick', date);
                    }
                }, {
                    text: '一周前',
                    onClick(picker) {
                        const date = new Date();
                        date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
                        picker.$emit('pick', date);
                    }
                }]
            },
            isLoading: false,
            selectedTaskIndex: "",
            tasks: [],
            selectedTask: {
                override: {}
            },
            params: {},
            searchParams: {
                keyword: '',
                page: 1,
                count: 1,
                size: 6,
                type: '',
                day: moment().format('YYYY-MM-DD'),
            },
            logs: [],
            timesOptions: [
                {
                    value: '00:00',
                    label: '00:00'
                },
                {
                    value: '01:00',
                    label: '01:00'
                },
                {
                    value: '06:00',
                    label: '06:00'
                },
                {
                    value: '08:00',
                    label: '08:00'
                },
                {
                    value: '12:00',
                    label: '12:00'
                },
                {
                    value: '16:00',
                    label: '16:00'
                },
                {
                    value: '18:00',
                    label: '18:00'
                },
                {
                    value: '20:00',
                    label: '20:00'
                },
                {
                    value: '22:00',
                    label: '22:00'
                },
                {
                    value: '23:00',
                    label: '23:00'
                }
            ],
            crontabConfig: {}
        }
    },
    mounted() {
        this.load()
        this.isPageActivation = true
        this.refreshTimer = window.setTimeout(() => {
            this.refresh()
        }, 1000)
    },
    methods: {
        editTask() {
            this.dialogVisible = true
            this.crontabConfig = this.selectedTask.override || {
                name: this.selectedTask.name,
                namespace: this.selectedTask.namespace,
                mode: this.selectedTask.mode,
                interval: this.selectedTask.interval || 0,
                times: this.selectedTask.times || [],
                status: 1
            }
        },
        saveTask() {
            this.isLoading = true
            this.$request.post('/crontab_override', this.$copy(this.crontabConfig)).then(result => {
                if (result.errCode) {
                    this.$message.error(result.message);
                    this.isLoading = false;
                } else {
                    this.$message.success('配置已更新')
                    this.dialogVisible = false
                }
            }).catch(error => {
                this.$message.error(error);
            }).then(() => {
                this.isLoading = false
            })
        },

        changeDay() {
            this.searchParams.page = 1
            this.logs = []
            this.getLogs()
        },
        getLogs() {
            this.isLoading = true;
            this.$request.get('/logs', this.searchParams).then(result => {
                if (result.errCode) {
                    this.$message.error(result.message);
                } else {
                    this.logs = result.data.list;
                    this.searchParams.count = result.data.total;
                }
            }).catch(error => {
                this.$message.error(error);
            }).finally(() => {
                this.isLoading = false;
            });
        },
        // 点击分页
        handleSizeChange(val) {
            this.searchParams.size = val;
            this.getLogs();
        },
        handleCurrentChange(val) {
            this.searchParams.page = val;
            this.getLogs();
        },
        load() {
            this.isLoading = true
            this.$request.get('/crontabs', this.$copy(this.params)).then(result => {
                if (result.errCode) {
                    this.$message.error(result.message);
                    this.isLoading = false;
                } else {
                    this.tasks = result.data
                    if (this.selectedTaskIndex === "") {
                        this.selectedTaskIndex = 0
                    }
                    this.displayTask()
                }
            }).catch(error => {
                this.$message.error(error);
            }).then(() => {
                setTimeout(() => {
                    this.isLoading = false
                }, 500)
            })
        },
        displayTask() {
            this.selectedTask = this.tasks[this.selectedTaskIndex]
            this.searchParams.page = 1
            this.searchParams.day = moment().format('YYYY-MM-DD')
            this.searchParams.type = this.selectedTask.namespace
            this.getLogs()
        },
        run() {
            this.isLoading = true
            this.$request.post('/crontab_run', {name: this.selectedTask.namespace}).then(result => {
                if (!result.errCode) {
                    this.$message.success('已请求立即执行')
                }
                setTimeout(() => {
                    this.getLogs()
                }, 3000)
            }).finally(() => {
                this.isLoading = false
            })
        },
        refresh() {
            clearTimeout(this.refreshTimer)
            if (!this.isPageActivation) {
                return
            }
            this.$request.post('/crontab_status', {name: this.selectedTask.namespace}).then(result => {
                if (!result.errCode) {
                    this.selectedTask = result.data
                }
            }).finally(() => {
                if (this.isPageActivation) {
                    this.refreshTimer = window.setTimeout(() => {
                        this.refresh()
                    }, 1000)
                }
            })
        }
    },
    beforeDestroy() {
        window.clearTimeout(this.refreshTimer)
        this.refreshTimer = null
        this.isPageActivation = false
    },
    destroyed() {
        window.clearTimeout(this.refreshTimer)
        this.refreshTimer = null
        this.isPageActivation = false
    }
}
</script>

